import React from 'react'

import {
  UserContainer,
  BlogList,
  LinkContainer
} from './UserStyles'

const User = ({ user, blogs }) => {
  if ( user === undefined) {
    return null
  }

  return (
    <UserContainer>
      <h1>{user.name}</h1>
      <p><strong>added blogs</strong></p>
      <BlogList>
        {blogs.map(blog => (<LinkContainer key={blog.id} to={`/api/blogs/${blog.id}`}>{blog.title}</LinkContainer>))}
      </BlogList>
    </UserContainer>
  )
}

export default User