import React, { useState } from 'react'
import { useField } from '../../hooks/index'

import {
  LoginFormContainer,
  FormContainer,
  InputContainer,
  Inputs,
  Buttons,
  Break
} from './LoginFormStyles'

const LoginForm = ({ onSignIn, onSignUp }) => {
  const username = useField('text')
  const name = useField('text')
  const password = useField('password')

  const [registration, setRegistration] = useState(false)

  const handleSwitch = (event) => {
    event.preventDefault()
    setRegistration(registration => !registration)
  }

  return (
    <LoginFormContainer auth={registration}>
      <h3>{ registration ? 'Register' : 'Log in to application' }</h3>
      <FormContainer onSubmit={registration ?
        event => {
          onSignUp(event, { name: name.intputAttributes.value, username: username.intputAttributes.value, password: password.intputAttributes.value })
          name.reset()
          username.reset()
          password.reset()
        }
        :
        event => {
          onSignIn(event, { username: username.intputAttributes.value, password: password.intputAttributes.value })
          username.reset()
          password.reset()
        }
      }>
        <Inputs>
          {registration &&
            <InputContainer>
              <p>name: <input data-cy='name' {...name.intputAttributes} /></p>
            </InputContainer>
          }
          <InputContainer>
            <p>username: <input data-cy='username' {...username.intputAttributes} /></p>
          </InputContainer>
          <InputContainer>
            <p>password: <input data-cy='password' {...password.intputAttributes} /></p>
          </InputContainer>
        </Inputs>
        <Buttons>
          <button type='submit'>{registration ? 'Sign Up' : 'Sign In'}</button>
          <Break>or</Break>
          <button type='button' onClick={handleSwitch}>{registration ? 'Sign In' : 'Sign Up'}</button>
        </Buttons>
      </FormContainer>
    </LoginFormContainer>
  )
}

export default LoginForm