import React from 'react'
import { useField } from '../../hooks/index'

import {
  BlogFormContainer,
  FormContainer,
  InputContainer
} from './BlogFormStyles'

const BlogForm = ({ onSubmit }) => {
  const title = useField('text')
  const author = useField('text')
  const url = useField('text')

  return (
    <BlogFormContainer>
      <h2>create new</h2>
      <FormContainer onSubmit={(event) => {
        onSubmit(event, { title: title.intputAttributes.value, author: author.intputAttributes.value, url: url.intputAttributes.value })
        title.reset()
        author.reset()
        url.reset()
      }}>
        <InputContainer>
          <p>title: <input data-cy='blog-title' {...title.intputAttributes} /></p>
        </InputContainer>
        <InputContainer>
          <p>author: <input data-cy='blog-author' {...author.intputAttributes} /></p>
        </InputContainer>
        <InputContainer>
          <p>url: <input data-cy='blog-url' {...url.intputAttributes} /></p>
        </InputContainer>
        <button type="submit" data-cy='blog-submit'>create</button>
      </FormContainer>
    </BlogFormContainer>
  )
}

export default BlogForm