import React from 'react'
import { connect } from 'react-redux'

import BlogPreview from '../BlogPreview/BlogPreview'

import {
  BlogsContainer,
  BlogList,
  LinkContainer
} from './BlogsStyles'

const Blogs = (props) => (
  <BlogsContainer>
    <h4>Top blogs</h4>
    <BlogList>
      {props.blogs.sort((a, b) => b.likes - a.likes).map(blog =>
        <LinkContainer key={blog.id} to={`/api/blogs/${blog.id}`} data-cy='blog-link'>
          <BlogPreview
            className='blog'
            blog={blog}
          />
        </LinkContainer>
      )}
    </BlogList>
  </BlogsContainer>
)

const mapStateToProps = (state) => {
  return {
    blogs: state.blogs
  }
}

export default connect(mapStateToProps)(Blogs)