import React, { useEffect, createRef } from 'react'
import { connect } from 'react-redux'
import {
  BrowserRouter as Router,
  Route,
  Redirect
} from 'react-router-dom'
import loginService from './services/login'
import blogsService from './services/blogs'
import registerService from './services/register'

import { initializeBlogs, addBlog, updateBlog, removeBlog } from './reducers/blogReducer'
import { initializeUsers } from './reducers/usersReducer'
import { login, logout } from './reducers/userReducer'
import { setNotif, clearNotif } from './reducers/notificationReducer'

import Blog from './components/Blog/Blog'
import Blogs from './components/Blogs/Blogs'
import BlogForm from './components/BlogForm/BlogForm'
import Header from './components/Header/Header'
import LoginForm from './components/LoginForm/LoginForm'
import Main from './components/Main/Main'
import Footer from './components/Footer/Footer'
import Togglable from './components/Togglable/Togglable'
import Users from './components/Users/Users'
import User from './components/User/User'

import './App.css'
import { Fragment } from 'react'

const App = (props) => {
  const blogFormRef = createRef()

  useEffect(() => {
    props.initializeBlogs()
    props.initializeUsers()
  }, [])

  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem('loggedBlogListUser')

    if(loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON)
      props.login(user)
      blogsService.setToken(user.token)
    }
  }, [])

  const notify = (message, status) => {
    props.setNotif(message, status)
    setTimeout(() => {
      props.clearNotif()
    }, 5000)
  }

  const handleRegister = async (event, user) => {
    event.preventDefault()
    try {
      await registerService.register(user)
      props.initializeUsers()
      const loggedUser = await loginService.login({ username: user.username, password: user.password })

      window.localStorage.setItem(
        'loggedBlogListUser', JSON.stringify(loggedUser)
      )
      blogsService.setToken(loggedUser.token)
      props.login(loggedUser)
    } catch (exception) {
      console.log(exception)
    }
  }

  const handleLogin = async (event, user) => {
    event.preventDefault()
    try {
      const loggedUser = await loginService.login(user)

      window.localStorage.setItem(
        'loggedBlogListUser', JSON.stringify(loggedUser)
      )
      blogsService.setToken(loggedUser.token)
      props.login(loggedUser)
    } catch (exception) {
      console.log(exception)
      notify('Wrong username or password', 'error')
    }
  }

  const handleLogout = event => {
    event.preventDefault()
    window.localStorage.removeItem('loggedBlogListUser')
    props.logout()
  }

  const handleBlogCreation = (event, newBlogObject) => {
    event.preventDefault()
    blogFormRef.current.toggleVisibility()
    try {
      props.addBlog(newBlogObject)
      props.initializeUsers()
      notify(`a new blog ${newBlogObject.title} by ${newBlogObject.author} added`, 'success')
    } catch (exception) {
      console.log(exception)
      notify('Unable to create', 'error')
    }
  }

  const handleLikeButton = (event, id) => {
    event.preventDefault()
    try {
      const blog = props.blogs.find(b => b.id === id)
      console.log(blog)
      const updatedBlog = { ...blog, likes: blog.likes + 1 }
      props.updateBlog(updatedBlog)
    } catch (exception) {
      console.log(exception)
    }
  }

  return (
    <Router>
      <div>
        <Header user={props.user} handleLogout={handleLogout} />
        <Main>
          <Route exact path='/' render={() =>
            <Fragment>
              <Blogs />
              <Togglable buttonLabel='add blog' ref={blogFormRef}>
                <BlogForm onSubmit={handleBlogCreation} />
              </Togglable>
            </Fragment>}
          />
          <Route path='/api/auth'>
            { props.user ? <Redirect to='/' /> : <LoginForm onSignIn={handleLogin} onSignUp={handleRegister} /> }
          </Route>
          <Route exact path='/api/users' render={() =>
            <Users users={props.users}/> }
          />
          <Route path='/api/users/:id' render={({ match }) =>
            <User user={props.users.find(user => user.id === match.params.id)} blogs={props.blogs.filter(blog => blog.user.id === match.params.id)} /> }
          />
          <Route path='/api/blogs/:id' render={({ match }) =>
            <Blog
              blog={props.blogs.find(blog => blog.id === match.params.id)}
              handleUpdate={handleLikeButton}
              notify={notify}
            />}
          />
        </Main>
        <Footer />
      </div>
    </Router>
  )
}

const mapStateToProps = (state) => {
  return {
    blogs: state.blogs,
    user: state.user,
    notification: state.notification,
    users: state.users
  }
}

const mapDispatchToProps = {
  initializeBlogs,
  initializeUsers,
  login,
  logout,
  addBlog,
  updateBlog,
  removeBlog,
  setNotif,
  clearNotif
}

export default connect(mapStateToProps, mapDispatchToProps)(App)
