import React from 'react'

import {
  UsersContainer,
  UserList,
  LinkContainer
} from './UsersStyles'

const Users = ({ users }) => (
  <UsersContainer>
    <h1>Users</h1>
    <UserList>
      {users.sort((a, b) => b.blogs.length - a.blogs.length).map(user => <LinkContainer key={user.id} to={`/api/users/${user.id}`}>{user.name} <span>({user.blogs.length} posts)</span></LinkContainer>)}
    </UserList>
  </UsersContainer>
)

export default Users