import React from 'react'

import { NavContainer, LinkContainer, Logout } from './NavStyles'

const Nav = ({ user, handleLogout }) => {

  return (
    <NavContainer>
      <LinkContainer to='/'>Blogs</LinkContainer>
      <LinkContainer to='/api/users'>Users</LinkContainer>
      { user
        ? <Logout><p>{user.name} <span>is logged in</span></p><button onClick={handleLogout}>Logout</button></Logout>
        : <LinkContainer to='/api/auth'>Login</LinkContainer>
      }
    </NavContainer>
  )
}

export default Nav