const notificationReducer = (state = [null, null], action) => {
  switch (action.type) {
  case 'SET':
    return [ action.data.message, action.data.status]
  case 'CLEAR':
    return [null, null]
  default:
    return state
  }
}

export const setNotif = (message, status) => {
  return {
    type: 'SET',
    data: {
      message,
      status
    }
  }
}

export const clearNotif = () => {
  return {
    type: 'CLEAR'
  }
}

export default notificationReducer