import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import blogsService from '../../services/blogs'
import { removeBlog } from '../../reducers/blogReducer'
import { useField } from '../../hooks/index'

import {
  BlogContainer,
  BlogInfoContainer,
  BlogHeader,
  BlogContent,
  CommentsContainer,
  CommentsList,
  CommentsForm
} from './BlogStyles'

const Blog = (props) => {
  const [comments, setComments] = useState([])
  const comment = useField('text')

  useEffect(() => {
    if (!props.blog) return
    blogsService.getComments(props.blog.id)
      .then(response => setComments(response))
  }, [props.blog])

  const handleRemoveButton = (event, { title, author, id }) => {
    event.preventDefault()
    if (window.confirm(`remove blog ${title} by ${author}`)) {
      try {
        props.removeBlog(id)
        props.history.push('/')
      } catch (exception) {
        console.log(exception)
      }
    }
  }

  const handleComment = async (event, { comment, blog }) => {
    event.preventDefault()
    try {
      const returnedComment = await blogsService.comment(blog.id, comment)
      setComments([returnedComment, ...comments])
      props.notify(`commented on '${blog.title}'`, 'success')
    } catch (exception) {
      console.log(exception)
      props.notify('couldn\'t comment', 'error')
    }
  }

  if ( props.blog === undefined) {
    return null
  }

  return (
    <BlogContainer>
      <BlogInfoContainer>
        <BlogHeader>
          <div>
            <h2>{props.blog.title}</h2>
            <h3>{`written by ${props.blog.author}`}</h3>
          </div>
          { props.user && props.user.name === props.blog.user.name && <button onClick={(event) => handleRemoveButton(event, props.blog)}>remove</button>}
        </BlogHeader>
        <BlogContent>
          <a href={props.blog.url}>go to site</a>
          <p>{props.blog.likes} likes <button onClick={(event) => props.handleUpdate(event, props.blog.id)} data-cy='like'>like</button></p>
          <p>added by {props.blog.user.name}</p>
        </BlogContent>
      </BlogInfoContainer>
      <CommentsContainer>
        <h3>comments</h3>
        <CommentsForm onSubmit={(event) => {
          handleComment(event, { comment: comment.intputAttributes.value, blog: props.blog })
          comment.reset()
        }}>
          <input {...comment.intputAttributes} />
          <button type="submit">comment</button>
        </CommentsForm>
        <CommentsList>
          {comments.reverse().map(comment => <li key={comment.id}>{comment.content}</li>)}
        </CommentsList>
      </CommentsContainer>
    </BlogContainer>
  )
}

const mapStateToProps = (state, ownProps) => {
  return {
    blog: ownProps.blog,
    handleUpdate: ownProps.handleUpdate,
    user: state.user,
    notify: ownProps.notify
  }
}

const mapDispatchToProps = {
  removeBlog
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Blog))